.profileCard {
    width: 100%;
    height: 100%;
    padding: 20px;
    border-radius: 20px;
    background-color: #26292a;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    color: #666666;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.profileCardContent {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #666666;
    padding-top: 20px;
}

.profileCardContent:last-child {
    border: unset;
}
.count {
    color: #eb608f;
}