
.wrappContainer {
    padding: 50px;
    background-color: var(--gray-color);
    width: 100%;
    height: 100%;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;

}
@media screen and (max-width: 768px) {
    .wrappContainer {
        padding: 20px;
    }
    
}